* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    width: 100%;
    min-height: 100vh;
    background: #e5e5e5;
}

.app {
    width: 400px;
    min-height: 500px;
    position: absolute;
    top: 50px;
    left: 50%; 
    transform: translateX(-50%);
    border-radius: 20px;
    box-shadow:  10px 10px 13px #0002,
                 -10px -10px 13px #fff7;
}

form {
    margin:  20px;
    height: 40px;
    width: 100%;
}

form input,
form button{
    width: 250px;
    height: 100%;
    border: none;
    outline: none;
    background: transparent;
    border-radius: 2px;
    padding: 0 2px;
    box-shadow: inset -2px -2px 2px #fff7,
                inset 2px 2px 2px #0002;

}

form button{
    width: 80px;
    margin-left: 15px;
    letter-spacing: 2px;
    cursor: pointer;
    box-shadow:  -2px -2px 2px #fff7,
                 2px 2px 2px #0002;
}
h1{
    text-align:  center;
    font-size: 2.5em;
    text-transform: uppercase;
    color: #e5e5e5;
    margin-bottom: 20px;
    text-shadow:  -3px -3px 3px #fff7,
                   3px 3px 3px #0003;

}
ul li{
    list-style: none;
    margin: 15px 0;
    padding: 10px;
    text-transform:  capitalize;
    box-shadow: -2px -2px 2px #fff7,
                2px 2px 2px #0002;
    animation: opacity 0.3s linear;  
}
@keyframes opacity{
  from{
    opacity: 0;
    transform: scale(0.7);
  }
}

ul li .row{
  float: right;
}
ul li .row i {
  color: teal;
  margin-left: 15px;
  cursor: pointer;
}
ul li .row i:hover {
  color: crimson;
}
.active{
  animation: del 0.3s linear;
}
@keyframes del{
  to{
    opacity: 0;
    transform: rotate(15deg) skew(50deg) scale(0.5);
  }
}
h2{
  font-size: 2em;
  text-align: center;
  color: teal;
  margin-top: 20px;
  margin-bottom: 10px;
  text-transform: capitalize;
}

ul li input{
  border: none;
  outline: none;
  background: transparent;
  width: 70%;
  box-shadow: inset -2px -2px 2px #fff7,
              inset 2px 2px 2px #0002;
}